import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import user from './util/user'

let axios;
if (process.env.NODE_ENV === 'production') {
    axios = Axios.create({
        baseURL: process.env.VUE_APP_BASE_API,
        timeout: 10000
    });
} else axios= Axios;

Vue.prototype.$axios = axios;
Vue.use(Vuex);

const KEY_TOKEN = 'TOKEN';

let store = new Vuex.Store({
    state: {
        token: localStorage.getItem(KEY_TOKEN) || '',
        logged: false,
        user: {
            id: 0,
            uuid: '',
            email: '',
            nickname: '',
            createdAt: 0,
            permission: -404,
            avatarId: 0,
        }
    },
    getters: {
        getUserPermissionClass: (state) => {
            return user.permission.wrapClass(state.user.permission);
        },
        getUserPermissionText: (state) => {
            return user.permission.wrapText(state.user.permission);
        },
    },
    mutations: {
        SET_TOKEN (state, { token, local }) {
            state.token = token;
            if (local === true)
                localStorage.setItem(KEY_TOKEN, token);
        },
        CLEAR_TOKEN (state) {
            state.token = '';
            localStorage.removeItem(KEY_TOKEN);
        },
        SET_LOGGED (state, logged) {
            state.logged = logged;
        },
        SET_USER (state, payload) {
            state.user = payload;
        },
        SET_USER_AVATAR (state, avatarId) {
            state.user.avatarId = avatarId;
        }
    },
    actions: {
        async login ({ dispatch, commit, state }, { email, password, remember }) {
            return axios.post('/api/login', {
                email, password
            }).then(res => {
                commit('SET_LOGGED', true);
                commit('SET_USER', res.data.data.user);
                commit('SET_TOKEN', { token: res.data.data.token, local: remember });
                return Promise.resolve(res);
            }).catch(err => {
                commit('SET_LOGGED', false);
                return Promise.reject(err);
            });
        },
        async verify ({ dispatch, commit, state }) {
            return axios.get('/api/verify').then(res => {
                commit('SET_LOGGED', true);
                commit('SET_USER', res.data.data.user);
                return Promise.resolve(res);
            }).catch(err => {
                commit('SET_LOGGED', false);
                commit('CLEAR_TOKEN');
                return Promise.reject(err);
            })
        },
        async logout ({ dispatch, commit, state }) {
            // TODO 后端吊销 JWT 令牌
            commit('SET_LOGGED', false);
            commit('SET_USER', {});
            commit('CLEAR_TOKEN');
            return Promise.resolve();
        },
        async register ({ dispatch, commit, state }, { email, password, nickname, code }) {
            return axios.post('/api/register', {
                email, password, nickname, code
            });
        },
        async getPlayers ({ dispatch, commit, state }) {
            return axios.get('/api/players');
        },
        async addPlayer ({ dispatch, commit, state }, { name, model }) {
            return axios.post('/api/players', {
                name, model
            });
        },
        async editPlayer ({ dispatch, commit, state }, { id, name, model }) {
            return axios.put(`/api/players/${id}`, {
                name, model
            });
        },
        async delPlayer ({ dispatch, commit, state }, { id }) {
            return axios.delete(`/api/players/${id}`);
        },
        async getCloset ({ dispatch, commit, state }, { category, page, perPage, query }) {
            return axios.get('/api/closets', {
                params: {
                    category,
                    page,
                    perPage,
                    query
                }
            });
        },
        async addCloset ({ dispatch, commit, state }, { name, type, model, isPublic, file }) {
            return axios.post('/api/closets', {
                name, type, model, public: isPublic, file
            });
        },
        async setAvatar ({ dispatch, commit, state }, { avatarId }) {
            commit('SET_USER_AVATAR', avatarId);
        }
    }
});

axios.interceptors.request.use(config => {
    let token = store.state.token;
    if (token !== '')
        config.headers.Authorization = `Bearer ${token}`;
    return config;
});

export default store;
