import Vue from 'vue'
import Router from 'vue-router'
import Index from './pages/Index.vue'

Vue.use(Router);

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/t',
            component: () => import('./pages/test/TestIndex.vue')
        },
        {
            path: '/',
            component: Index,
            meta: {
                optionalLogged: true
            }
        },
        {
            path: '/login',
            component: () => import('./pages/auth/AuthLogin.vue'),
            meta: {
                optionalLogged: true,
                skipLogged: true
            }
        },
        {
            path: '/register',
            component: () => import('./pages/auth/AuthRegister.vue'),
            meta: {
                optionalLogged: true,
                skipLogged: true
            }
        },
        {
            path: '/forget',
            component: () => import('./pages/auth/AuthForget.vue'),
            meta: {
                optionalLogged: true,
                skipLogged: true
            }
        },
        {
            path: '/user',
            component: () => import('./pages/user/UserIndex.vue'),
            meta: {
                requireLogged: true
            },
            children: [
                {
                    path: '/user/',
                    component: () => import('./pages/user/UserDashboard.vue'),
                    meta: {
                        requireLogged: true
                    },
                },
                {
                    path: '/user/profile',
                    component: () => import('./pages/user/UserProfile.vue'),
                    meta: {
                        requireLogged: true
                    },
                },
                {
                    path: '/user/closet',
                    component: () => import('./pages/user/UserCloset.vue'),
                    meta: {
                        requireLogged: true
                    }
                },
                {
                    path: '/user/player',
                    component: () => import('./pages/user/UserPlayer.vue'),
                    meta: {
                        requireLogged: true
                    }
                }
            ]
        }
    ]
})
