module.exports = {
    permission: {
        wrapClass: (permission) => {
            switch (permission) {
                case -503: return 'red--text';
                case 0: return 'green--text';
                case 1: return 'orange--text';
                case 2: return 'pink--text';
                case -504:
                default:
                    return 'grey--text';
            }
        },
        wrapText: (permission) => {
            switch (permission) {
                case -503: return '已封禁';
                case 0: return '普通用户';
                case 1: return '管理员';
                case 2: return '超级管理员';
                case -504:
                default:
                    return '未知用户';
            }
        },
    }
};
