import Vue from 'vue'
import Vuetify from 'vuetify'
import VuetifyZhHans from 'vuetify/es5/locale/zh-Hans'
import App from './App.vue'
import router from './router'
import store from './store'
import nprogress from 'nprogress'
import 'vuetify/src/stylus/app.styl'
import 'vuetify/dist/vuetify.min.css'
import '@mdi/font/css/materialdesignicons.min.css'
import 'nprogress/nprogress.css'
import './styles/index.styl'

Vue.config.productionTip = false;
Vue.use(Vuetify, {
    options: {
        customProperties: true
    },
    iconfont: 'mdi',
    lang: {
        locales: {VuetifyZhHans},
        current: 'zh-Hans'
    }
});

nprogress.inc(0.2);
nprogress.configure({ easing: 'ease', speed: 500, showSpinner: true });

router.beforeEach((to, from, next) => {
    nprogress.start();
    if (store.state.logged) {
        if (to.meta.skipLogged === true) {
            next({ path: '/user' })
        } else
            next();
        nprogress.done();
        return;
    }
    let token = store.state.token;
    if (to.meta.requireLogged === true) {
        if (token === '')
            next({ path: '/login', query: { redirect: to.fullPath } });
        else {
            store.dispatch('verify')
                .then(() => next())
                .catch(() => next({ path: '/login', query: { redirect: to.fullPath } }))
        }
    } else if (to.meta.optionalLogged === true) {
        if (token === '')
            next();
        else {
            store.dispatch('verify')
                .then(() => {
                    if (to.path === '/login') next({ path: '/user' });
                    else next();
                })
                .catch(() => next())
        }
    } else {
        next();
    }
});

router.afterEach(() => {
    nprogress.done();
});

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');


let version = process.env.VUE_APP_VERSION;
