<template lang="pug">
    v-app(light)
        v-toolbar(dark fixed flat dense)
            v-container.toolbar-center
                v-layout(align-center justify-center)
                    v-toolbar-title.minecraft {{ title }}
                    v-spacer
                    router-link(v-if="$store.state.logged" to="/user").user-avatar
                        v-flex(row)
                            v-avatar(size="24px" tile)
                                UserAvatar
                            span.minecraft.nickname {{ $store.state.user.nickname }}
                    v-btn(v-else icon dark to="/login")
                        v-icon mdi-account-circle
        v-content
            section
                v-parallax(src="<%= BASE_URL %>/images/background.png" style="height: 100vh;")
                    v-layout(column align-center justify-center)
                        h1.display-3.minecraft.mcclake {{ title }}
                        v-subheader.white--text {{ subTitle }}
                        v-btn(v-if="$store.state.logged" dark outline large to="/user").font-weight-bold {{ started.center }}
                        v-btn(v-else dark outline large to="/login").font-weight-bold {{ started.join }}
            section.py-5
                v-layout(column wrap align-center)
                    v-flex(xs12 sm4).py-3
                        .text-xs-center
                            h2.display-1 {{ feature.name }}
                v-layout(row wrap align-center)
                    v-flex(xs12 md6 v-for="entry in feature.entries" :key="entry.name")
                        v-card(flat).transparent.text-xs-center
                            v-card-text
                                v-icon(x-large style="font-size: 5rem;") {{ entry.icon }}
                            v-card-title(primary-title).justify-center
                                .headline.text-xs-center {{ entry.name }}
                            v-card-text {{ entry.value }}
        Footer(powered)
</template>

<script>
    import UserAvatar from '@/components/user/UserAvatar.vue'
    import Footer from '@/components/common/Footer.vue'
    export default {
        name: 'Index',
        components: {
            UserAvatar, Footer
        },
        data: () => ({
            title: 'l2yggdrasil',
            subTitle: 'Vue.js & Vuetify.js 提供动力支持',
            menu: null,
            started: {
                join: '现在加入',
                center: '用户中心'
            },
            feature: {
                name: '功能特性',
                entries: [
                    { icon: 'mdi-account-multiple', name: '多角色', value: '一个账户可以拥有多个游戏角色' },
                    { icon: 'mdi-share-variant', name: '分享', value: '浏览皮肤库，添加喜爱的皮肤并与好友分享' },
                ]
            },
        })
    }
</script>

<style scoped lang="stylus">
    .mcclake
        font-size 4.5rem !important
        @media (max-width 768px)
            font-size 2.2rem !important
    .toolbar-center
        margin auto !important
        padding-top 0
        padding-bottom 0
        @media (max-width 768px)
            margin 0
            padding 0
    .user-avatar
        .nickname
            display inline-flex
            line-height 24px
            vertical-align middle
            padding-left 0.6rem
            color white
            transition color ease .4s
        &:hover
            .nickname
                color #1976d2
</style>
